/* Core Styles   */  

@import 'partials/mixins';   

* {
	box-sizing: border-box;
}

[class*="col-c-"] { 
	float: left;
	padding: 15px;
	width: 100%;
}

@include min600 {
	/* For tablets: */
	.col-c-1 {width: 8.33%;}
	.col-c-2 {width: 16.66%;}
	.col-c-3 {width: 25%;}
	.col-c-4 {width: 33.33%;}
	.col-c-5 {width: 41.66%;}
	.col-c-6 {width: 50%;}
	.col-c-7 {width: 58.33%;}
	.col-c-8 {width: 66.66%;}
	.col-c-9 {width: 75%;}
	.col-c-10 {width: 83.33%;}
	.col-c-11 {width: 91.66%;}
	.col-c-12 {width: 100%;}

	.tcol1 {width: 8.33%;}
	.tcol2 {width: 16.66%;}
	.tcol3 { width:25%;}
	.tcol7 {width: 58.33%;}
}

h2	{
	margin-top:0;
}

#container-main {
	visibility:hidden;
}

@include max767 {
	#container-main {	
		width:99%;
	}
}

.clear-both {
	clear:both;
}

.loadimage {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	display:block;
	width:100%;
	height:300px;
	background-image: url('/images/loading1.gif');
	background-repeat: no-repeat;
	background-position:center;
}

.row:after {
	content: "";
	clear: both;
	display: block; 
}

.alt-active	{
	background-color:#f5f5f5;
	font-weight:bold;
}

.relative	{
	position:relative;
	margin:0;
	padding:0;
}

@include max767 {
	.signature {
		padding-left:15px;
	}
}

.signature-name {
	font-style:italic;
	font-weight:bold;
	text-decoration:underline; 
}

@media only screen and (max-width: 600px) {
	.navbar-header {
		padding-bottom:0;
	}
}

.navbar-nav {
	padding:8px 0 0 0;
	.open {
		.dropdown-menu {
			left:-65px;
			background-color: #fff;
			border: 1px solid rgba(0,0,0,0.15);
			box-shadow: 0 6px 12px rgba(0,0,0,0.175);
		}
		@include max767 {
			.dropdown-menu {
				position: absolute;
				margin-top: 2px;
			}
		}
		@include min768 {
			.dropdown-menu {
			    right: inherit; 
			}
		}
	}
}

@include max767 {
	.navbar-nav	{
		margin:0;
		padding-top:0;
		padding-bottom:0;
	}
}

@include min600 {
	#siteHeader .navbar-nav {
		float: right!important;
	}
}

#contentView {
  position:relative;
  min-height:300px;
}

.body-container {
	margin-bottom:40px; 
}

.intro {
	margin-bottom:20px;
}

pre.view-code	{
	font-size:10px;
}

.modal-link	{
	cursor:pointer;
	text-align:right;
	color:red;
	text-decoration:underline;
	float:right;
}

.modal-header {
	.nav-modal {
		margin:0;
		li span	{
			padding: 0 10px 0 0;
			cursor:pointer;
			font-size:13px;
		}
		@include min275 {
			li {
				float:left;
			}
		}
	}
}

@include min768 {
	.modal-header	{
		height:60px;
	}
	.modal-dialog {
		width: 700px;
	}
}

xmp	{
	white-space:pre-wrap;
}
